.sp-no-outline {
  outline: none;
}
.sp-drawer-mask {
  /* background-color: transparent !important; */
}
#sp-danmu-input {
  margin-left: 30px;
  margin-right: 10px;
  display: inline-block;
}
.sp-danmu-input-textarea {
  font-size: 1.1rem !important;
}
.margin-5 {
  margin: 5px;
}
.sp-button {
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: .007px;
  text-transform: none !important;
  border-radius: 2px !important; 
  margin: 5px !important;
}

.sp-comment-count {
  /* font-size: 12px; */
  /* font-weight: 500; */
  margin-left: 5px;
}

#sp-danmu-bar {
  /* display: none; */
}
.sp-duonao-bar {
  margin-bottom: 35px;
}
.sp-youtube-bar {
  margin-top: 20px;
}
.sp-danmu-input-timestamp {
  line-height: 50px;
  font-size: 1.2rem;
  border-radius: 2px;
  padding: 5px;
  padding-left: 8px;
  padding-right: 8px;
}
.sp-youtube-bar .sp-danmu-input-timestamp {
  line-height: 45px;
}
.sp-danmu-input-btn-group {
  display: block;
  text-align: right;
  /* margin-top: -10px; */
}
.sp-danmu-input-not-login {
  display: block;
  text-align: right;
  color: rgb(0, 132, 255);
}
.sp-danmu-input-not-login:hover {
  text-decoration: underline;
  cursor: pointer;
}
.sp-danmu-input-sent {
  display: block;
  text-align: right;
  font-size: 1.2rem;
  /* margin-top: -10px; */
  /* color: rgb(0, 119, 255); */
}
.sp-danmu-input-sent.success {
  color: rgb(0, 132, 255);
}
.sp-danmu-input-sent.fail {
  color: red;
}
