.sp-no-outline {
  outline: none;
}
.sp-drawer-mask {
  /* background-color: transparent !important; */
}
#sp-danmu-input {
  margin-left: 30px;
  margin-right: 10px;
  display: inline-block;
}
.sp-danmu-input-textarea {
  font-size: 1.1rem !important;
}
.margin-5 {
  margin: 5px;
}
.sp-button {
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: .007px;
  text-transform: none !important;
  border-radius: 2px !important; 
  margin: 5px !important;
}

.sp-comment-count {
  /* font-size: 12px; */
  /* font-weight: 500; */
  margin-left: 5px;
}

#sp-danmu-bar {
  /* display: none; */
}
.sp-duonao-bar {
  margin-bottom: 35px;
}
.sp-youtube-bar {
  margin-top: 20px;
}
.sp-danmu-input-timestamp {
  line-height: 50px;
  font-size: 1.2rem;
  border-radius: 2px;
  padding: 5px;
  padding-left: 8px;
  padding-right: 8px;
}
.sp-youtube-bar .sp-danmu-input-timestamp {
  line-height: 45px;
}
.sp-danmu-input-btn-group {
  display: block;
  text-align: right;
  /* margin-top: -10px; */
}
.sp-danmu-input-not-login {
  display: block;
  text-align: right;
  color: rgb(0, 132, 255);
}
.sp-danmu-input-not-login:hover {
  text-decoration: underline;
  cursor: pointer;
}
.sp-danmu-input-sent {
  display: block;
  text-align: right;
  font-size: 1.2rem;
  /* margin-top: -10px; */
  /* color: rgb(0, 119, 255); */
}
.sp-danmu-input-sent.success {
  color: rgb(0, 132, 255);
}
.sp-danmu-input-sent.fail {
  color: red;
}

.sp-danmu-wrapper {
  display: flex;
  position: fixed;
  cursor: pointer;
  z-index: 2147483647;
}
.sp-danmu-avatar {
  /* display: inline-block; */
  border-radius: 100%;
  width: 35px;
  height: 35px;
  object-fit: cover;
  border: 1px solid lightgray;
}
.sp-danmu-content-text {
  border: 1px solid lightgray;
  background-color: white !important;
  color: black !important;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
  line-height: 25px;
  border-radius: 5px;
  max-width: 500px;
  margin-left: 5px;
  /* border: none; */
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  box-shadow: 7px 7px 3px rgba(0, 0, 0, 0.2);
}
.sp-danmu-content-text.self {
  background-color: #bf0 !important;
  color: black !important;
}
.sp-danmu-content-img {
  width: 100px;
  height: 100px;
  margin-top: -10px;
}
.inline-emoji {
  /* font-size: 18px; */
  /* bottom: -3px; */
  /* position: relative; */
}

.sp-chatbox-iframe {
  /* don't remove anything, this is fighting other sites' styling*/
  min-height: 0px;
  min-width: 0px;
  height: 100%;
  width: 100%;
  max-width: 9999px;
  max-height: 9999px;
  border: none;
  /* put display: none here, but still need to set it in js
  so that we can read it easily later
  */
  margin-bottom: 0px;
}
.sp-chatbox-iframe-wrapper {
  position: fixed !important;
  left: 0;
  bottom: 0;
  z-index: 2147483647;
  display: none;
  box-shadow: 15px 15px 5px rgba(0, 0, 0, 0.31);

  /* height: 450px;
  width: 330px; specified in code */
}

